

















import { Vue, Component, Prop, Emit } from 'vue-property-decorator'

@Component
export default class ParamConfig extends Vue {
    //  param 
    @Prop({
        required: true,
        default: {
            match: '',
            default: '',
            label: ''
        }
    })
    value!: CommandParam

    // 
    @Prop({
        required: true
    })
    show!: boolean

    // 
    inputLimit = 20

    // label 
    rules = [(v: string) => !v || v.length <= this.inputLimit || '']

    /**
     * 
     *  param 
     */
    @Emit('on-confirm')
    confirm(): CommandParam {
        //  label 
        if (this.value.label === '') this.value.label = ''
        if (this.value.label.length > this.inputLimit) this.value.label = this.value.label.substring(0, this.inputLimit)

        return this.value
    }

    // 
    @Emit('on-cancel')
    cancel() { }
}
