

































































/**
 * 
 * 
 */
import ScreepsApi from '@/plugins/screepsApi'
import Storage from '@/plugins/storage'
import { Component, Prop, Emit, Mixins } from 'vue-property-decorator'

@Component
export default class Boot extends Mixins(ScreepsApi) {
    //  token 
    tokenCreateUrl = 'https://screeps.com/a/#!/account/auth-tokens'

    // 
    introduceContent = [
        {
            title: '',
            text: ' / '
        },
        {
            title: '',
            text: ' Screeps '
        },
        {
            title: '',
            text: ' github'
        }
    ]

    // 
    // tab 0  token 1  v-tabs 
    loginTab: 0 | 1 = 0

    // 
    step = 1

    // 
    @Prop()
    show!: boolean

    // 
    passwordVisiable = false

    // token
    token = ''
    email = ''
    password = ''

    // 
    sessionToken = ''

    // 
    confirmBtnLoading = false

    // 
    get currentTitle () {
        switch (this.step) {
        case 1: return ''
        case 2: return ''
        case 3: return ''
        default: return ''
        }
    }

    /**
     * 
     * 
     */
    @Emit('on-finish')
    finishBoot(): LoginSuccessEvent {
        Storage.init()

        return {
            token: this.sessionToken,
            type: this.loginTab === 0 ? 'token' : 'account'
        }
    }

    /**
     *  - 
     */
    next() {
        if (this.step === 2) this.login()
        else if (this.step === 3) this.finishBoot()
        else this.step++
    }

    /**
     * 
     * 
     */
    login() {
        if (this.confirmBtnLoading) return

        if (this.loginTab === 0) this.tokenAuth()
        else if (this.loginTab === 1) this.accountAuth()
    }

    /**
     *  token 
     * 
     *  token 
     */
    tokenAuth() {
        if (!this.token) return

        // 
        this.confirmBtnLoading = true
        this.$toast.info(' Screeps ...')

        this.getPlayerInfo(this.token).then(resp => {
            //  token  token  seesionToken 
            Storage.setToken(this.token)
            this.sessionToken = resp.sessionToken

            this.step++
            this.$toast.success('')
            this.confirmBtnLoading = false
        }).catch((e: Error) => {
            this.confirmBtnLoading = false
            this.$toast.error(e.message.includes('401') ? ' token ' : ', : ' + e.message)
        })
    }

    /**
     * 
     *  sessionToken token  authToken 
     * 
     */
    accountAuth() {
        if (!this.email || !this.password) return

        // 
        this.confirmBtnLoading = true
        this.$toast.info(' Screeps ...')

        // 
        this.getSessionToken(this.email, this.password).then(token => {
            this.sessionToken = token
            this.step++
            this.$toast.success('')
            this.confirmBtnLoading = false
        }).catch((e: Error) => {
            this.confirmBtnLoading = false
            this.$toast.error(e.message.includes('401') ? '' : ', : ' + e.message)
        })
    }

    // 
    openPage(url: string) {
        window.open(url)
    }
}
