











/**
 *  token 
 */
import Storage from '@/plugins/storage'
import { Vue, Component, Emit } from 'vue-property-decorator'

@Component
export default class TokenSetter extends Vue {
    //  token
    newToken = ''

    // 
    //  true 
    statsConfirm = false

    // 
    get introduce() {
        return this.statsConfirm ? ' Token'
            : ' AuthToken  token'
    }

    // 
    get btnText() {
        return this.statsConfirm ? '' : ''
    }

    // 
    get btnColor() {
        return this.statsConfirm ? 'error' : 'primary'
    }

    // 
    confirmImport(): void {
        if (this.statsConfirm) this.importToken()
        else this.statsConfirm = true
    }

    // 
    cancelLoad(): void {
        this.statsConfirm = false
    }

    // 
    importToken() {
        Storage.setToken(this.newToken)

        this.statsConfirm = false

        this.finish()
        this.newToken = ''
    }

    @Emit('on-finish')
    finish(): SidebarEmitEvent {
        return {
            show: true,
            content: this.newToken ? ', ' : '',
            color: 'success'
        }
    }
}
