

















import { Vue, Component, Prop, Emit } from 'vue-property-decorator'

@Component
export default class ButtonHelp extends Vue {
    // 
    info = [
        {
            title: '',
            content: ' screeps ',
            code: 'Object.key(Game.creeps).length'
        },
        {
            title: '',
            content: '  '
        },
        {
            title: '',
            content: ' $ ',
            code: 'Game.rooms.$1.controller.level'
        }
    ]

    // 
    @Prop({
        required: true
    })
    show!: boolean

    //  - 
    @Emit('on-close')
    onClose() { }
}
