



























import { Vue, Component, Prop, Emit, Watch } from 'vue-property-decorator'
import SidebarComp from './sidebarComp'

@Component({
    components: { ...SidebarComp }
})
export default class Navigation extends Vue {
    // 
    items = [
        { title: '', comp: 'button-edit', icon: 'mdi-plus-box-multiple', persistent: true },
        { title: '', comp: 'button-config', icon: 'mdi-cursor-default-click', persistent: false },
        { title: ' shard', comp: 'shard-setter', icon: 'mdi-server', persistent: false },
        { title: ' AuthToken', comp: 'token-setter', icon: 'mdi-alpha-t-box', persistent: false },
        { title: '', comp: 'save-config', icon: 'mdi-download-multiple', persistent: false },
        { title: '', comp: 'load-config', icon: 'mdi-upload-multiple', persistent: false },
        { title: '', comp: 'about', icon: 'mdi-help-box', persistent: false }
    ]

    // 
    @Prop({
        required: true
    })
    show = false

    //  prop 
    @Watch('show')
    syncShow(show: boolean) {
        this.sidebarDrawerVisiable = show
    }

    // 
    @Watch('sidebarDrawerVisiable')
    emitCloseEvent(newData: boolean) {
        if (!newData) this.closeDrawer()
    }

    //  dialog
    sidebarVisiable = false
    // 
    sidebarDrawerVisiable = false
    //  dialog 
    sidebarPersistent = false

    // 
    activeSidebar = 'save-config'

    /**
     *  - 
     *
     * @param compName 
     * @param persistent 
     */
    onClickSidebar(compName: string, persistent: boolean) {
        this.sidebarVisiable = true
        this.sidebarDrawerVisiable = false
        this.activeSidebar = compName
        this.sidebarPersistent = persistent
    }

    /**
     *  - 
     */
    onSidebarFinish(e: SidebarEmitEvent) {
        this.sidebarVisiable = false

        if (!e.show) return

        // this.message(e.color || 'success', e.content || '')
        this.$toast(e.content || '', {
            color: e.color
        })
    }

    //  - 
    @Emit('on-close')
    closeDrawer() { }
}
