









































import { Component, Mixins } from 'vue-property-decorator'
import ScreepsApi from '@/plugins/screepsApi'
import Storage from '@/plugins/storage'

import Boot from '@/components/Boot.vue'
import Login from '@/components/Login.vue'
import ConsoleItem from '@/components/ConsoleItem.vue'
import CommandList from '@/components/CommandList.vue'

@Component({
    components: { ConsoleItem, CommandList, Boot, Login }
})
export default class Console extends Mixins(ScreepsApi) {
    // 
    messageList: ConsoleMessage[] = []

    // 
    commandListVisiable = false

    // 
    inputCommand = ''

    // screeps  ws 
    screepsWebSock!: WebSocket

    // socket 
    keepSockTimer!: number

    // screeps  token token 
    screepsToken = ''

    // 
    bootVisable = false
    // 
    loginVisable = false

    // 
    // 
    fillBlockVisiable = false

    /**
     *  - 
     *
     * @param data 
     */
    pasteMessage(data: string[]) {
        this.inputCommand = data.join('\n')
    }

    /**
     *  - 
     */
    onCommandSend() {
        //  shard
        this.sendCommand(this.inputCommand, Storage.get().shard)

        this.inputCommand = ''
    }

    /**
     * 
     *
     * @param command 
     * @param shard  shard
     */
    sendCommand(command: string, shard: string) {
        if (!command || command.length <= 0) return

        const message = this.addNewMessage([command], 'mdi-arrow-top-left-thick', true, shard)

        // 
        this.sendConsoleExpression(command, shard).then(resp => {
            console.log('', resp)
            message.loading = false

            if (resp.data.error) {
                message.icon = 'mdi-alert-circle'
                this.addNewMessage([': ' + resp.data.error], 'mdi-alert-circle', false, shard)
            }
        }).catch(error => {
            message.loading = false
            message.icon = 'mdi-alert-circle'
            this.addNewMessage(['', error], 'mdi-alert-circle', false, shard)
        })

        this.scrollToBottom()
    }

    /**
     *  - 
     *
     * @param entries 
     */
    onIntersect(entries: IntersectionObserverEntry[]) {
        // 
        this.fillBlockVisiable = entries[0].isIntersecting
    }

    // 
    scrollToBottom() {
        console.log('Console -> scrollToBottom -> this.$refs.itemList', this.$refs.itemList)
        this.$vuetify.goTo(this.$refs.itemList as HTMLElement, { duration: 1000 })
    }

    /**
     *  - 
     */
    getCommand(e: GetCommandEvent) {
        this.commandListVisiable = false

        // 
        this.sendCommand(e.command, e.shard)
    }

    /**
     *  - ws 
     *
     * @param e 
     */
    onMessage(e: MessageEvent) {
        // 
        if (e.data[0] !== 'a') return

        try {
            //  [0] 
            const dataStr = JSON.parse(e.data.substring(1))[0]
            //  [1]  id
            const data: ScreepsConsoleMessage = JSON.parse(dataStr)[1]
            // console.log('results', data.messages.results, 'log', data.messages.log)
            const logs: string[] = []

            // 
            if (data.error) {
                this.addNewMessage(data.error.split('\n'), 'mdi-alert-circle', false, data.shard)
            }
            else {
                if (data.messages.log.length > 0) {
                    //  \n  html 
                    logs.push(...data.messages.log.map(log => log.split('\n')).flat())
                }
                if (data.messages.results.length > 0) logs.push(...data.messages.results[0].split('\n'))

                //  screeps ws  tick 
                if (logs.length <= 0) return

                // 
                this.addNewMessage(logs, 'mdi-arrow-bottom-right-thick', false, data.shard)
            }
        }
        catch (error) {
            console.log('onMessage ', error, e)
        }

        // 
        // 
        if (this.fillBlockVisiable) this.scrollToBottom()
    }

    /**
     * 
     *
     * @param content 
     * @param icon 
     * @return 
     */
    addNewMessage(content: string[], icon: string, loading = true, shard = ''): ConsoleMessage {
        const date = new Date()

        const message: ConsoleMessage = {
            content,
            icon,
            loading,
            shard,
            date: `${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`
        }
        this.messageList.push(message)
        return message
    }

    /**
     *  - 
     */
    onLoginSuccess(e: LoginSuccessEvent) {
        this.bootVisable = false
        this.loginVisable = false

        this.addNewMessage([e.type === 'token' ? ' AuthToken' : ''], 'mdi-key', false)
        const wsMessage = this.addNewMessage([' Screeps WebSocket, ...'], 'mdi-wifi')
        //  token 
        this.screepsToken = e.token

        this.createSocket(e.token, wsMessage)
    }

    /**
     *  screeps socket 
     *
     * @param token  token
     * @param message  socket 
     */
    createSocket(token: string, message: ConsoleMessage) {
        //  screeps 
        //  ws 
        this.initScreepsApi(token).then(ws => {
            ws.onmessage = this.onMessage
            message.loading = false
            message.content = ['Screeps WebSocket !']

            this.addNewMessage([''], 'mdi-wifi', false)

            //  sock 
            this.keepSockTimer = setInterval(this.checkSockAlive, 1000)
        }).catch((e: Error) => {
            console.log(' ws !', e)
            message.loading = false
            message.icon = 'mdi-alert-circle'
            message.content = ['Screeps WebSocket , ']

            if (e.message.includes('401')) this.addNewMessage([' token '], 'mdi-alert-circle', false)
        })
    }

    /**
     *  - 
     * 
     *
     * @param item 
     */
    onItemClick(item: ConsoleMessage) {
        const shardCotent = item.shard ? ` ${item.shard} | ` : ''
        this.$toast.info(`${shardCotent} ${item.date}`, {
            y: 'top'
        })
    }

    mounted() {
        // , 
        if (!Storage.exist) this.bootVisable = true
        else {
            Storage.init()

            const token = Storage.get().token
            //  token  ws
            if (!token) this.loginVisable = true
            else this.onLoginSuccess({ token, type: 'token' })
        }
    }

    /**
     *  sock 
     * 
     */
    checkSockAlive() {
        //  socket 
        if (!document.hidden && this.screepsWebSock) {
            const sockState = this.screepsWebSock.readyState
            if (sockState === 2 || sockState === 3) this.reloadSocket()
        }
    }

    /**
     *  socket
     */
    reloadSocket() {
        const wsMessage = this.addNewMessage(['...'], 'mdi-wifi')
        this.createSocket(this.screepsToken, wsMessage)

        // 
        clearInterval(this.keepSockTimer)
    }

    destroyed() {
        this.closeWebSocket()
    }
}
